.navbar{
    z-index: 1;
}
.badge{
    position: 'absolute';
    background-color: rgb(245, 5, 5);
    width: 16;
    height: 16;
    border-radius: 15 / 2;
    right: 10;
    top: +5;
    align-items: 'center';
    justify-content: 'center';
    color: white;

}
.text{
    align-items: 'center';
    justify-content: 'center';
    color: "#FFFFFF";
    font-size: 8;
}
.subnav-content {
    display: none;
    position: absolute;
    left: 0;
    background-color: rgb(255, 255, 255);
    width: 100%;
    z-index: 1;
  }
  
  .subnav-content a {
    float: left;
    color: rgb(0, 0, 0);
    text-decoration: none;
  }
  
  .subnav-content a:hover {
    background-color: #eee;
    color: black;
  }
  
  .subnav:hover .subnav-content {
    display: block;
  }
  body {
    font-family: Arial, Helvetica, sans-serif;
    margin: 0;
  }
  
  .nav {
    overflow: hidden;
    background-color: #fdfcfc; 
  }
  
  .nav a {
    float: left;
    font-size: 16px;
    color: rgb(46, 43, 43);
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }